#link-style {
	font-weight: bold;
}

.header-color {
	background: #485563;
	background: -webkit-linear-gradient(to right, #29323c, #485563);
	background: linear-gradient(to right, #29323c, #485563);
}

.landing-grid {
	background: #485563;
	background: -webkit-linear-gradient(to right, #29323c, #485563);
	background: linear-gradient(to right, #29323c, #485563);
	text-align: center;
	height: 100vh;
}

.avatar-image {
	margin-top: 100px;
	/* margin: auto;     */
	height: 250px;
}

.banner-text {
	background-color: black;
	opacity: .7;
	width: 85%;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: auto;
	border-radius: 10px;
}

.banner-text h1 {
	font-size: 66px;
	font-weight: bold;
	color: rgb(255, 255, 255);
	text-align: center;
}

.banner-text hr {
	border-top: 5px dotted white;
	width: 70%;
	margin: auto;
}

.banner-text p {
	color: white;
	font-size: 20px;
	padding: 1em;
	text-align: center;
}

.social-links {
	display: flex;
	justify-content: space-evenly;
	width: 50%;
	margin: auto;
}

.social-links i {
	color: white;
	font-size: 3em;
	padding: 8px;
}

/* Página de CONTATO */

.contact-body {
	background: #485563;
	background: -webkit-linear-gradient(to right, #29323c, #485563);
	background: linear-gradient(to right, #29323c, #485563);
	margin: auto;
	position: fixed;
	width: 100%;
	height: 100%;
	justify-content: center;
	padding-top: 2em;
}

.contact-grid {
	text-align: center;
	justify-content: center;
	width: 80%;
	height: 550px;
	background: white;
	color: black;
	border: 2px solid #29323c;
	margin-bottom: 20px;
}

.contact-grid h2 {
	font-family: 'Anton', sans-serif;
}

.contact-grid p {
	font-family: 'Oxygen', sans-serif;
	font-weight: bold;
}

.contact-grid hr {
	border-top: 5px dotted black;
	width: 50%;
	margin: auto;
}

.lista-contato i {
	font-size: 66px;
	padding-right: 5rem;
}

/* Página do Curriculo CSS */

.resume-col-dir {
	background: #27221F;
	color: white;
	padding: 2em;
}

@media only screen and (max-width: 376px) {
	.banner-text h1 {
		font-size: 40px;
	}
}

.projects-grid {
	display: flex;
}

@media only screen and (min-width: 1024px) {
	.material-icons {
		display: none;
	}
}